




























import { Component, Vue } from 'vue-property-decorator';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { SMAAPass } from 'three/examples/jsm/postprocessing/SMAAPass.js';
import { Mesh, Material, Sprite, Vector3 } from 'three';
import anime from 'animejs';

@Component<Scene>({
    mounted() {
        const sceneRef = this.$refs.scene as Element;

        // Sprite
        this.addAllSprites();

        // Ustawienia kamer
        this.cameras['global'].fov = 60;
        this.cameras['global'].aspect = sceneRef.clientWidth / sceneRef.clientHeight;
        this.cameras['global'].near = 1;
        this.cameras['global'].far = 1000;
        this.cameras['global'].position.set(55, 55, 55);

        this.cameras['person'].fov = 60;
        this.cameras['person'].aspect = sceneRef.clientWidth / sceneRef.clientHeight;
        this.cameras['person'].near = 1;
        this.cameras['person'].far = 1000;
        this.cameras['person'].position.set(76, 1, 76);

        this.cameras[this.currentCamera].updateProjectionMatrix();
        this.renderer.setSize(sceneRef.clientWidth, sceneRef.clientHeight);
        sceneRef.appendChild(this.renderer.domElement);

        // Ustawienia sterów kamery
        this.controls['global'].enableDamping = true;
        this.controls['global'].dampingFactor = 0.05;
        this.controls['global'].screenSpacePanning = false;
        this.controls['global'].minDistance = 10;
        this.controls['global'].maxDistance = 5000;
        this.controls['global'].maxPolarAngle = Math.PI / 2;
        this.controls['global'].enablePan = true;
        this.controls['global'].enableKeys = false;
        this.controls['person'].target = new THREE.Vector3(0, 0, 0);

        this.controls['person'].enableDamping = true;
        this.controls['person'].dampingFactor = 0.05;
        this.controls['person'].screenSpacePanning = false;
        this.controls['person'].minDistance = 10;
        this.controls['person'].maxDistance = 5000;
        this.controls['person'].maxPolarAngle = Math.PI / 2;
        this.controls['person'].enablePan = true;
        this.controls['person'].target = new THREE.Vector3(75, 1, 75);
        this.controls['person'].enableKeys = true;
        this.controls['person'].keyPanSpeed = 70;
        this.controls['person'].enableZoom = false;

        // Ustawienia ekranu dotykowego
        if ('ontouchstart' in document.documentElement) {
            this.touchScreen = true;
            window.addEventListener('touchend', this.onCursorMove, false);
        } else {
            // Mouse event
            window.addEventListener('mousemove', this.onCursorMove, false);
        }

        // Widnow event
        window.addEventListener('resize', this.onWindowResize);

        // Dodanie loadera plików
        const loader = new GLTFLoader(this.manager);

        // Draco
        const dracoLoader = new DRACOLoader();
        dracoLoader.setDecoderPath('gltf/');
        loader.setDRACOLoader(dracoLoader);

        loader.load(
            'develop_house/untitledDraco.gltf',
            gltf => {
                const model = gltf.scene;
                this.scene.add(model);
            },
            undefined,
            function(error) {
                console.error(error);
            },
        );

        loader.load(
            'develop_house/sciana.gltf',
            gltf => {
                const model = gltf.scene;
                model.name = 'sciana';
                this.scene.add(model);
                for (const mesh in model.children) {
                    (model.children[mesh] as Mesh).material = new THREE.MeshStandardMaterial({ color: 0xff0000, opacity: 0, transparent: true });
                    this.selectable.push(model.children[mesh] as Mesh);
                }
            },
            undefined,
            function(error) {
                console.error(error);
            },
        );

        // Manager
        this.manager.onProgress = (item, loaded, total) => {
            this.loadingLoaded = loaded;
            this.loadingTotal = total;
            this.loadingProgress = Math.round((loaded / total) * 10000) / 100;
        };

        // Dodanie światła
        const ambientLight = new THREE.HemisphereLight(0xffffff, 0x222222, 1);
        const mainLight = new THREE.DirectionalLight(0xffffff, 0.5);
        mainLight.position.set(10, 10, 10);
        this.scene.add(ambientLight, mainLight);

        // Tło
        const textureLoader = new THREE.TextureLoader();
        const groundTexture = textureLoader.load('textures/grass.jpg');
        groundTexture.wrapS = groundTexture.wrapT = THREE.RepeatWrapping;
        groundTexture.repeat.set(25, 25);
        groundTexture.anisotropy = 16;
        groundTexture.encoding = THREE.sRGBEncoding;

        const groundMaterial = new THREE.MeshLambertMaterial({ map: groundTexture });

        const mesh = new THREE.Mesh(new THREE.PlaneBufferGeometry(500, 500), groundMaterial);
        mesh.position.y = -1;
        mesh.rotation.x = -Math.PI / 2;
        mesh.receiveShadow = true;

        this.scene.background = new THREE.Color(0xcce0ff);
        this.scene.fog = new THREE.Fog(0xcce0ff, 70, 200);
        this.scene.add(mesh);

        // Postprocessing
        const composer = new EffectComposer(this.renderer);
        const renderPass = new RenderPass(this.scene, this.cameras[this.currentCamera]);
        composer.addPass(renderPass);
        const pass = new SMAAPass(window.innerWidth * this.renderer.getPixelRatio(), window.innerHeight * this.renderer.getPixelRatio());
        composer.addPass(pass);

        // Funkcja renderująca
        const render = () => {
            this.renderer.render(this.scene, this.cameras[this.currentCamera]);
            this.updateAnnotationPosition();
        };

        // Główna pętla sceny
        const animate = () => {
            requestAnimationFrame(animate);
            this.controls[this.currentCamera].update(this.clock.getDelta());
            //composer.render(); // Render postprocessingu
            render(); // Render standardowy
        };

        animate();
    },
    destroyed: function() {
        window.addEventListener('mousemove', this.onCursorMove, false);
        this.renderer.forceContextLoss();
        this.currentSelected = null; // Inaczej nie zatrzymuje updateAnnotationPosition()
    },
})
export default class Scene extends Vue {
    currentCamera = 'global';
    private renderer: THREE.WebGLRenderer = new THREE.WebGLRenderer({ antialias: true });
    private cameras: { [key: string]: THREE.PerspectiveCamera } = {
        global: new THREE.PerspectiveCamera(),
        person: new THREE.PerspectiveCamera(),
    };
    private controls: { [key: string]: any } = {
        global: new OrbitControls(this.cameras['global'], this.renderer.domElement),
        person: new OrbitControls(this.cameras['person'], this.renderer.domElement),
    };
    private scene: THREE.Scene = new THREE.Scene();
    private mouse = new THREE.Vector2();
    private raycaster = new THREE.Raycaster();
    private clock = new THREE.Clock();
    manager = new THREE.LoadingManager();
    selectable: Mesh[] = [];
    currentHover: number | null = null;
    currentSelected: number | null = null;
    touchScreen = false;
    loadingLoaded = 0;
    loadingTotal = 0;
    loadingProgress = 0;
    private animation?: anime.AnimeInstance;
    currentSelectedSprite: THREE.Intersection | null = null;
    addedSprites: THREE.Sprite[] = [];
    sprites: { id: number; position: THREE.Vector3 }[] = [
        {
            id: 1,
            position: new THREE.Vector3(50, 18, -12),
        },
        {
            id: 2,
            position: new THREE.Vector3(8, 20, -14),
        },
        {
            id: 3,
            position: new THREE.Vector3(10, 15, -8),
        },
    ];

    resetOpacity() {
        if (this.currentHover) {
            const index = this.selectable.findIndex(mesh => mesh.id === this.currentHover);
            if (this.currentHover != this.currentSelected) {
                (this.selectable[index].material as Material).opacity = 0;
            }
            this.currentHover = null;
        }
    }

    onCursorMove(event: any) {
        if (this.touchScreen) {
            this.mouse.x = (event.changedTouches[0].clientX / window.innerWidth) * 2 - 1;
            this.mouse.y = -(event.changedTouches[0].clientY / window.innerHeight) * 2 + 1;
        } else {
            this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
            this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
        }

        // Raycasting
        this.raycaster.setFromCamera(this.mouse, this.cameras[this.currentCamera]);

        // Przecięcia mieszkań wykryte przez raycasting
        const intersects = this.raycaster.intersectObjects(this.selectable, true);
        if (intersects && intersects[0]) {
            this.resetOpacity();
            ((intersects[0].object as Mesh).material as Material).opacity = 0.5;
            ((intersects[0].object as Mesh).material as Material).transparent = true;
            this.currentHover = intersects[0].object.id;
        } else {
            this.resetOpacity();
            const index = this.selectable.findIndex(mesh => mesh.id === this.currentSelected);
            for (const mesh in this.selectable) {
                if (mesh != index.toString()) {
                    (this.selectable[mesh].material as Material).opacity = 0;
                }
            }
        }

        // Przecięcia punktów wykryte przez raycasting
        const spritesIntersects = this.raycaster.intersectObjects(this.addedSprites, true);
        if (spritesIntersects && spritesIntersects[0]) {
            ((spritesIntersects[0].object as Mesh).material as Material).opacity = 0.1;
            this.currentSelectedSprite = spritesIntersects[0];
        } else {
            if (this.currentSelectedSprite) {
                ((this.currentSelectedSprite.object as Mesh).material as Material).opacity = 0.7;
                this.currentSelectedSprite = null;
            }
        }
    }

    onWindowResize() {
        this.cameras[this.currentCamera].aspect = window.innerWidth / window.innerHeight;
        this.cameras[this.currentCamera].updateProjectionMatrix();
        this.renderer.setSize(window.innerWidth, window.innerHeight);
    }

    clickScreen() {
        if (this.currentHover) {
            if (this.currentHover === this.currentSelected) {
                this.$router.push('FlatScene');
            } else {
                this.currentSelected = this.currentHover;
            }
        }

        if (this.currentSelectedSprite) {
            this.moveCameraToPosition(this.currentCamera, this.currentSelectedSprite.object.position, 1500);
        }
    }

    moveCameraToPosition(camera: string, newPosition: Vector3, duration: number, oncomplete?: Function) {
        const currentAngle = this.controls[camera].getAzimuthalAngle();
        this.controls[camera].maxAzimuthAngle = currentAngle;
        this.controls[camera].minAzimuthAngle = currentAngle;
        this.controls[camera].enableRotate = false;

        this.animation = anime({
            targets: this.cameras[camera].position,
            easing: 'easeOutCirc',
            duration: duration,
            complete: () => {
                this.animation = undefined;
            },
            x: newPosition.x,
            y: newPosition.y,
            z: newPosition.z - 10,
        });

        this.animation = anime({
            targets: this.controls[camera].target,
            easing: 'easeOutCirc',
            duration: duration + 10,
            complete: () => {
                this.animation = undefined;
                this.controls[camera].maxAzimuthAngle = Infinity;
                this.controls[camera].minAzimuthAngle = -Infinity;
                this.controls[camera].enableRotate = true;
                if (oncomplete != undefined) {
                    oncomplete();
                }
            },
            x: newPosition.x,
            y: newPosition.y,
            z: newPosition.z,
        });
    }
    changeCamera(startCamera: string, endCamera: string, duration: number) {
        this.controls[startCamera].saveState();
        this.controls[startCamera].enableRotate = false;

        this.animation = anime({
            targets: this.cameras[startCamera].position,
            easing: 'easeOutCirc',
            duration: duration,
            complete: () => {
                this.animation = undefined;
            },
            x: this.cameras[endCamera].position.x,
            y: this.cameras[endCamera].position.y,
            z: this.cameras[endCamera].position.z,
        });

        this.animation = anime({
            targets: this.controls[startCamera].target,
            easing: 'easeOutCirc',
            duration: duration * 1.1,
            complete: () => {
                this.animation = undefined;
                this.controls[startCamera].enableRotate = true;
                this.currentCamera = endCamera;
                this.cameras[endCamera].updateProjectionMatrix();
                this.controls[startCamera].reset();
            },
            x: this.controls[endCamera].target.x,
            y: this.controls[endCamera].target.y,
            z: this.controls[endCamera].target.z,
        });
    }

    drawNumber(number: string) {
        const canvas = document.createElement('canvas');
        canvas.width = 64;
        canvas.height = 64;

        const x = 32;
        const y = 32;
        const radius = 30;
        const startAngle = 0;
        const endAngle = Math.PI * 2;

        const ctx = canvas.getContext('2d');
        if (ctx != null) {
            ctx.fillStyle = 'rgb(0, 0, 0)';
            ctx.beginPath();
            ctx.arc(x, y, radius, startAngle, endAngle);
            ctx.fill();

            ctx.strokeStyle = 'rgb(255, 255, 255)';
            ctx.lineWidth = 3;
            ctx.beginPath();
            ctx.arc(x, y, radius, startAngle, endAngle);
            ctx.stroke();

            ctx.fillStyle = 'rgb(255, 255, 255)';
            ctx.font = '32px sans-serif';
            ctx.textAlign = 'center';
            ctx.textBaseline = 'middle';
            ctx.fillText(number, x, y);
        }
        return new THREE.CanvasTexture(canvas);
    }

    addSprite(number: string, position: THREE.Vector3) {
        const numberTexture = this.drawNumber(number);

        const spriteMaterialFront = new THREE.SpriteMaterial({
            map: numberTexture,
        });

        const spriteMaterialBack = new THREE.SpriteMaterial({
            map: numberTexture,
            transparent: true,
            depthTest: false,
            opacity: 0.3,
        });

        const spriteFront = new THREE.Sprite(spriteMaterialFront);
        const spriteBack = new THREE.Sprite(spriteMaterialBack);

        spriteFront.position.copy(position);
        spriteBack.position.copy(position);

        spriteFront.userData.frontID = spriteFront.id;
        spriteFront.userData.backID = spriteBack.id;
        spriteBack.userData.frontID = spriteFront.id;
        spriteBack.userData.backID = spriteBack.id;

        this.addedSprites.push(spriteFront, spriteBack);
        this.scene.add(spriteFront, spriteBack);
    }

    addAllSprites() {
        for (const key in this.sprites) {
            this.addSprite(this.sprites[key].id.toString(), this.sprites[key].position);
        }
    }

    updateAnnotationPosition() {
        if (this.currentSelected) {
            const index = this.selectable.findIndex(mesh => mesh.id === this.currentSelected);
            const vector = this.selectable[index].position.clone();
            const canvas = this.renderer.domElement;

            vector.project(this.cameras[this.currentCamera]);

            vector.x = Math.round((0.5 + vector.x / 2) * (canvas.width / window.devicePixelRatio));
            vector.y = Math.round((0.5 - vector.y / 2) * (canvas.height / window.devicePixelRatio));

            (this.$refs.annotation as HTMLElement).style.top = `${vector.y}px`;
            (this.$refs.annotation as HTMLElement).style.left = `${vector.x}px`;
        }
    }

    closeAnnotation() {
        this.currentSelected = null;
    }

    clickCurrentSelected() {
        this.$router.push('FlatScene');
    }
}
